









































































































































































































































































































































import { Component, Vue, Watch } from 'vue-property-decorator'
import ResizableView from '../../../components/utility/ResizableView.vue'
import { readOperationsActivities, readOperationsOneCustomer } from '../../../store/operations/getters'
import { dispatchGetCustomerActivities } from '../../../store/operations/actions'
import { ICustomerSearch } from '../../../interfaces/index'

@Component({
  components: {
    ResizableView,
  },
})
export default class Activity extends Vue {
  public headers = [
    {
      text: 'Session ID',
      sortable: true,
      value: 'sessionId',
      align: 'center',
    },
    {
      text: 'Time',
      sortable: true,
      value: 'time',
      align: 'center',
    },
    {
      text: 'Activity',
      sortable: true,
      value: 'activity',
      align: 'center',
    },
    {
      text: 'Activity Details',
      sortable: true,
      value: 'activityDetails',
      align: 'center',
    },
    {
      text: 'Reference ID',
      sortable: true,
      value: 'actionReference',
      align: 'center',
    },
    {
      text: 'Affect on Funds',
      sortable: true,
      value: 'affectOnFunds',
      align: 'center',
    },
    {
      text: 'Available Funds',
      sortable: true,
      value: 'availableFunds',
      align: 'center',
    },
  ]

  public searchByEmail = this.$route.params.searchByEmail || ''
  public searchedFields: ICustomerSearch =
    (this.$route.params.searchedFields as ICustomerSearch) || ({} as ICustomerSearch)

  public params: ICustomerSearch = {}
  public customerId
  public date = new Date().toISOString().substr(0, 10)
  public menuFrom = false
  public menuTo = false
  public dateFrom: string | null = ''
  public dateTo: string | null = ''
  public dateFormatted = this.formatDate(new Date().toISOString().substr(0, 10))
  public dates: any[] = []
  public tempDates: any[] = []
  public selectedRecords: any[] = []

  // traditional vue's lifecycle "watch" method
  @Watch('dates')
  public setDateFrom(dates) {
    this.dateFrom = this.formatDate(dates[0])
    this.dateTo = this.formatDate(dates[1])
  }

  @Watch('selectedCustomer')
  public setActivities() {
    console.log('@Watch for selectedCustomer triggered')
    this.selectedCustomer
  }

  get selectedCustomer() {
    return readOperationsOneCustomer(this.$store)
  }

  get customerActivities() {
    return readOperationsActivities(this.$store)
  }

  public async fetchCustomerActivities() {
    if (this.selectedCustomer) {
      // this.customer_id = this.selectedCustomer._id // references ObjectId
      this.customerId = this.selectedCustomer.id
      const activities = await dispatchGetCustomerActivities(this.$store, this.customerId)
      console.log('activities after mount: ', activities)
    }
  }

  public async mounted() {
    console.log('mounting activity page. customerId: ', this.selectedCustomer)
    if (this.selectedCustomer?.id) {
      await dispatchGetCustomerActivities(this.$store, this.selectedCustomer.id)
    }
  }

  get minCurrentMonth() {
    if (this.dates.length) {
      let current = new Date(this.dates[0])
      current.setDate(current.getDate() + 1)
      return current.toISOString().slice(0, 10)
    } else {
      return new Date().toISOString().slice(0, 10)
    }
  }

  // vuetify bug fix: subsequent date selections no longer overwrite each other
  public checkFirstSelectedDate() {
    this.tempDates = this.dates
    if (this.dates.length > 1) {
      this.dates.shift()
      return
    }
    this.menuFrom = false

    // resets selectedRecords list when user selects a date
    this.selectedRecords = []
  }

  public checkSecondSelectedDate() {
    if (this.dates.length === 1) {
      this.dates.unshift(this.tempDates[0] || this.dates[0])
    }
    this.menuTo = false

    // resets selectedRecords list when user selects a date
    this.selectedRecords = []
  }

  get maxDate() {
    return new Date().toISOString().slice(0, 10)
  }

  public formatDate(date) {
    if (!date) {
      return null
    }

    const [year, month, day] = date.split('-')
    return `${month}/${day}/${year}`
  }

  public parseDate(date) {
    if (!date) {
      return null
    }

    const [month, day, year] = date.split('/')
    return `${year}-${month.padStart(2, '0')}-${day.padStart(2, '0')}`
  }

  get filterActivities() {
    const filtered = this.customerActivities.filter(
      (activity) => activity.updatedDate < this.dates[1] && activity.updatedDate > this.dates[0],
    )
    return filtered
  }

  public clearSelectedDates() {
    this.dates = []
  }

  public toggleSelection(row) {
    if (this.selectedRecords.some((record) => record._id === row._id)) {
      this.selectedRecords = this.selectedRecords.filter((record) => record._id !== row._id)
    } else {
      this.selectedRecords.push(row)
    }
  }

  public selectAll() {
    const data = this.filterActivities.length > 0 ? this.filterActivities : this.customerActivities
    if (this.selectedRecords.length <= 0) {
      data.forEach((record) => {
        this.selectedRecords.push(record)
      })
    } else {
      this.selectedRecords = []
    }
  }

  public async logout() {
    // await dispatchUserLogOut(this.$store, this.$auth);
    console.log('clicked logout...')
  }
}
